.overlay {
	width: 100%;
	height: 100%;
	position: absolute;
	text-align: center;
	display: none;
	z-index: 1000 !important;
	/* background: #ccc8; */
}


h2 {
	font-size: 40px;
  text-align: center;
	width: 75%;
	/* background: #052b18; */
	padding: 13px;
	line-height: 40px;
	color: black;
}

h4 {
	margin-top: 20px;
    margin-right: 20px;
}

body {
    margin: 0;
    padding: 0;
    width: 100%;
    box-sizing: border-box;
    overflow-x: hidden; /* Prevent horizontal scrolling */
}


.row {
	margin-top: 10px;
    display: flex;
    flex-wrap: wrap;

}

.col-sm-1 {
    -ms-flex: 0 0 8.333333%;
    flex: 0 0 8.333333%;
    max-width: 8.333333%;
}

#init-label {
    width: 75%;
    text-align: left;
    margin-left: 20px; /* Adjust the value as needed */
    margin-top: 10px; /* Optional: Add top margin if needed */
  }

.form-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: 20px;
    margin-left: 20px;
    padding: 20px;
    width: 75%;
    box-sizing: border-box;
}

.form-row-input {
    display: flex;
    flex-wrap: wrap;
    padding-left: 20px;
    margin-right: 20px;
    margin-left: 20px;
    width: 100%;
    box-sizing: border-box;
}

#input_text {
    width: 75%;
    box-sizing: border-box; /* Include padding and border in the element's total width */
  }


.form-group label {
    display: block;
    margin-bottom: 5px; /* Optional: Add some space between the label and the text box */
  }
  
  .form-group {
    text-align: center;
    margin: 15px; /* Optional: Add some space between form groups */
  }

.form-group {
  text-align: center;
  margin: 15px; /* Optional: Add some space between form groups */
}



.options-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 15px; /* Add some space below the options */
}

.slider-container {
  margin-top: 15px; /* Add some space above the slider */
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align items to the start */
  width: 100%; /* Ensure the slider container takes the full width */
}

.form-group.col {
  width: 100%; /* Ensure the form group takes the full width */
  text-align: left; /* Align text to the left */
}

.top-k-words-slider {
  width: 100%; /* Set the width to 60% */
  max-width: 200; /* Optionally, set a max width for the slider */
  align-self: flex-start; /* Align the slider to the left */
}

.context-window-slider {
  width: 100%; /* Set the width to 130% */
  max-width: 600px; /* Optionally, set a max width for the slider */
  align-self: flex-start; /* Align the slider to the left */
}

.input-text-container, .suggested-text-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center the label and textarea */
  width: 100%;
  margin-bottom: 30px;
}

.text-box-label {
  text-align: center; /* Center the label text */
  margin-bottom: 10px; /* Optional: Add some space below the label */
}

.txt_input {
  width: 100%; /* Ensure the textarea takes the full width */
  text-align: left; /* Ensure text is left-aligned */
}

.txt_response {
  background-color: #FFF !important;
  height: 150px !important;
  width: 30%; /* Ensure the textarea takes the full width */
  text-align: left; /* Ensure text is left-aligned */
}

#max_paragraphs {
  margin-top: 7px !important;
}

